.header {
}


.header__row {
    border: 1px solid black;
    width: 280px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    // padding-top: 11px;
    // padding-bottom: 16px;
    margin-top: 11px;
    margin-bottom: 16px;
}
@media (min-width:768px) {
    .header__row{
        display: none;
    }
}

.header-tbl{
    display: none;
}
@media (min-width:768px) {
    .header-tbl {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 24px;
        padding-bottom: 16px;

        &__list {
            display: flex;
            position: relative;

            font-family: 'Oswald';
            font-weight: 400;
            text-transform: uppercase;
            font-size: 18px;

            li:not(:last-child){
                margin-right: 42px;
            }   
        }
        &__item--active{
            border-bottom: 2px solid $bg-color;
        }

        &__item:hover{
            opacity: 0.6;
            opacity: 0.6;
        }
        &__item:focus{
            opacity: 0.3;
        }
        &__item:active{
            opacity: 1;
        }

    }
}    


@media (min-width:1440px) {
    .header-tbl {
        &__list {
            font-size: 20px;  
            color: #fff;
        }
        &__item:hover{
            opacity: 0.6;
        }
        &__item:focus{
            opacity: 0.3;
        }
        &__item--active{
            opacity: 1;
            border-bottom: 2px solid #ffffff;
        }

    }
}




