
.header-new__inner{
    max-width: 280px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 11px auto 16px auto;
}


.header-new__logo-img{
    width: 191px;
    height: 38px;
}
@media(min-width:768px){
    .header-new__logo-img{
        width: 173px;
        height: 58px;
    }
}
@media(min-width:1440px){
    .header-new__logo-img{
        width: 202px;
        height: 59px;
    }
}



@media(min-width:768px){
    .header-new__burger{
        display: none;
    }
}
